import { Component } from 'react'
import {bitcoinService} from '../services/bitcoinService';
import {userService} from '../services/userService';
import {StatisticPage} from './StatisticPage'

export class HomePage extends Component {
    state={
        user:null,
        rate:null,
        marketPrice:'',
        confirmedTransactions:''
    }
    // We use arrow function where we want to callback the function like in button inside a child component for performance reasons.
    setStatsData = async () =>{
        const marketPrice = await bitcoinService.getMarketPrice();
        const confirmedTransactions = await bitcoinService.getConfirmedTransactions();
        this.setState({marketPrice, confirmedTransactions});
    }

    async componentDidMount(){
        const user = await userService.getUserById('skljtkl645kl7j');
        this.setState({ user }, this.setUserRate);
        this.setStatsData()
    }
   // We had to get rate AFTER getting user coins - So we had to get the user first, async.
    setUserRate = async () => {
        const rate = await bitcoinService.getRate(this.state.user.coins);
        this.setState({ rate });
      }

    render() {
        const { user, rate , marketPrice, confirmedTransactions } = this.state; 
        if (marketPrice) console.log('state marketPrice in homepage',marketPrice);
        if (!user && !rate) return <div className="page-loading-screen">Loading...</div>
        return (
            <section className="Home-Page">
                <div className="Home-Page-container">
                    <h2 className="hp-header">Hello {user.name}!</h2>
                    <div className="hp-usercoins-container">
                        <img src={require('../assets/imgs/icons/coins.png').default }alt="coins"/>
                        <h2>Amount (USD): {user.coins}</h2>
                    </div>
                    <div className="hp-btcrate-container">
                        <img src={require('../assets/imgs/icons/bitcoin.png').default }alt="bitcoin"/>
                        <h2>Value in BTC: {rate}</h2>                
                    </div>
                </div>
                {marketPrice &&  confirmedTransactions && <StatisticPage marketPrice={marketPrice} confirmedTransactions={confirmedTransactions}/>}
            </section>
            
        )
    }
}
