import { Component } from 'react'
import { ContactList } from '../cmps/ContactList';
import { ContactFilter } from '../cmps/ContactFilter';
import {contactService} from '../services/contactService'
import {ContactDetailsPage} from './ContactDetailsPage'
export class ContactPage extends Component {
    state={
        contacts:null,
        filterBy:{
            term:"",
        },
        currContact:null
    }

    componentDidMount(){
        this.loadContacts();
    }

    async loadContacts(){
        const contacts = await contactService.getContacts(this.state.filterBy);
        this.setState({contacts});
    }

    contactDetails = async (contactId) => {
        const currContact = await contactService.getContactById(contactId);
        this.setState({currContact});
    }

    
    onChangeFilter =(ev) =>{

        console.log(ev.target.value);
        this.setState(prevState => ({ filterBy: { ...prevState.filterBy, term: ev.target.value } }), this.loadContacts);       
    }

    backToList = () => {
        this.setState({ currContact: "" });
      };
   
    render() {
        const { contacts , currContact} = this.state
        if(!contacts) return <div className="page-loading-screen">Loading...</div>
        return (
            <div className="contact-page-container">
                {contacts && !currContact && <ContactFilter  onChangeFilter={this.onChangeFilter}/>}
                {contacts && !currContact && <ContactList contacts={contacts} onContactDetails={this.contactDetails}/>}
                {currContact && (
          <ContactDetailsPage contact={currContact} onBack={this.backToList} />
        )}
            </div>
        )
    }
}
