export const userService = {
  getUsers,
  getUserById,
  deleteUser,
  saveUser,
  getEmptyUser
}



const users = [
  {
    "_id": "6d7l45ca",
    "name": "Ofer",
    "coins": 50,
    moves: []
  },{
    "_id": "5a56640269flgu6o4uo4u7ca",
    "name": "Keren",
    "coins": 10,
    moves: []
  },{
    "_id": "skljtkl645kl7j",
    "name": "Uri",
    "coins": 200,
    moves: []
  },
];

function sort(arr) {
  return arr.sort((a, b) => {
    if (a.name.toLocaleLowerCase() < b.name.toLocaleLowerCase()) {
      return -1;
    }
    if (a.name.toLocaleLowerCase() > b.name.toLocaleLowerCase()) {
      return 1;
    }

    return 0;
  })
}

function getUsers(filterBy = null) {
  return new Promise((resolve, reject) => {
    var usersToReturn = users;
    if (filterBy && filterBy.term) {
      usersToReturn = filter(filterBy.term)
    }
    resolve(sort(usersToReturn))
  })
}

function getUserById(id) {
  return new Promise((resolve, reject) => {
    const user = users.find(user => user._id === id)
    user ? resolve(user) : reject(`User id ${id} not found!`)
  })
}

function deleteUser(id) {
  return new Promise((resolve, reject) => {
    const index = users.findIndex(user => user._id === id)
    if (index !== -1) {
      users.splice(index, 1)
    }

    resolve(users)
  })
}

function _updateUser(user) {
  return new Promise((resolve, reject) => {
    const index = users.findIndex(c => user._id === c._id)
    if (index !== -1) {
      users[index] = user
    }
    resolve(user)
  })
}

function _addUser(user) {
  return new Promise((resolve, reject) => {
    user._id = _makeId()
    users.push(user)
    resolve(user)
  })
}

function saveUser(user) {
  return user._id ? _updateUser(user) : _addUser(user)
}

function getEmptyUser() {
  return {
    name: '',
    email: '',
    phone: ''
  }
}

function filter(term) {
  term = term.toLocaleLowerCase()
  return users.filter(user => {
    return user.name.toLocaleLowerCase().includes(term)
  })
}



function _makeId(length = 10) {
  var txt = ''
  var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
  for (var i = 0; i < length; i++) {
    txt += possible.charAt(Math.floor(Math.random() * possible.length))
  }
  return txt
}