export const bitcoinService = {
    getRate,
    getMarketPrice,
    getConfirmedTransactions
}

async function getRate(coins, currency = 'USD') {
    try {
        return 0
        const res = await axios.get(`https://blockchain.info/tobtc?currency=${currency}&value=${coins}`)
        return res.data
    } catch (err) {
        console.log(err);
    }
}
async function getMarketPrice() {
    try {
      return gMarketPrice;
       const res = await axios.get(`https://api.blockchain.info/charts/market-price?timespan=12months&format=json&cors=true`)
      
       return res.data
    } catch (err) {
        console.log(err);
    }

}
async function getConfirmedTransactions() {
    try {
        return gConfirmedTrans;

         const res = await axios.get(`https://api.blockchain.info/charts/n-transactions?timespan=12months&format=json&cors=true`)
         return res.data
    } catch (err) {
        console.log(err);
    }
}

