import React from 'react'
import { Sparklines, SparklinesLine} from 'react-sparklines';
export function DailyTransChart({ confirmedTransactions }) {
    return (
      <section className="statistics">
      <div className="daily-trans-chart-container">
        <h3>Bitcoin Confirmed Transactions</h3>
        <Sparklines data={confirmedTransactions.values.map((value) => [value.y.toFixed()])} width={200}>
            <SparklinesLine style={{ strokeWidth: 1, stroke: "#336aff", fill: "none" }} />
        </Sparklines>
      </div>
       </section>
    );
  }
