import  { Component } from 'react'

export class ContactFilter extends Component {
    
    state = {
        // filterBy: {
        //     term: "",
        //   }
    }

    handleChange = async (ev) => {
        this.props.onChangeFilter(ev)
             
    };

    handleSubmit = (ev) => {
        ev.preventDefault();
      };
      
    
    render() {

        return (
            <form className="simple-form contact-filter" onSubmit={this.handleSubmit}>
        <label htmlFor="term"></label>
        <input type="search" name="term" onChange={this.handleChange} placeholder="Search by name/email/phone" autoComplete="off"/>
      </form>
        )
    }
}
