import React from 'react'
import { Sparklines, SparklinesLine ,SparklinesSpots, SparklinesNormalBand} from 'react-sparklines';
export function MarketPriceChart({ marketPrice }) {
    return (
      <section className="statistics">
        <div className="market-price-chart-container">
          <h3>Bitcoin Market-Price</h3>
          <Sparklines data={marketPrice.values.map((value) => [value.y.toFixed()])}>
              <SparklinesLine style={{ stroke: "#2991c8", fill: "none"}} />
              <SparklinesSpots />
            {/* <SparklinesNormalBand/> */}
          </Sparklines>
        </div>
       </section>
    );
  }
 
